import {iosVhFix} from './utils/ios-vh-fix';
import {initModals} from './modules/modals/init-modals';
import {initCustomSelect} from './modules/form/init-custom-select';
import {initFormValidate} from './modules/form/init-form-validate';
import {initScrollTo} from './modules/init-scroll-to';
import {initNotEmptyInputs} from './modules/form/not-empty-inputs';
import {initProjectsSlider} from './modules/sliders/init-projects-slider';
import {initClientsSlider} from './modules/sliders/init-clients-slider';
import {initAccordions} from './modules/init-accordion';
import {initConsentModalBtns} from './modules/modals/init-consent-modal-btns';
import {initRecaptcha} from './modules/init-recaptcha';

// ---------------------------------

window.addEventListener('DOMContentLoaded', () => {

  // Utils
  // ---------------------------------

  iosVhFix();

  // Modules
  // ---------------------------------

  // все скрипты должны быть в обработчике 'DOMContentLoaded', но не все в 'load'
  // в load следует добавить скрипты, не участвующие в работе первого экрана
  window.addEventListener('load', () => {
    initModals();
    initCustomSelect();
    initFormValidate();
    initNotEmptyInputs();
    initScrollTo();
    initProjectsSlider();
    initClientsSlider();
    initAccordions();
    initConsentModalBtns();
    initRecaptcha();
  });
});

